.text {
  position: relative;
  padding-top: 120px;
  padding-left: 10.8%;
  z-index: 1;

  @include viewport--lg {
    padding-top: 120px;
  }

  @media (max-width: 850px) {
    padding-top: 100px;
    padding-left: 5.5%;
  }

  &--benefit {
    padding-top: 105px;
    padding-left: 14.5%;

    @include viewport--lg {
      padding-top: 100px;
      padding-left: 9.5%;
    }

    @media (max-width: 400px) {
      padding-top: 76px;
      padding-left: 4.5%;
    }
  }
}

.text__title {
  display: flex;
  flex-direction: column;
  align-items: center;

  max-width: 512px;
  width: 39.6%;
  min-height: 200px;
  margin-bottom: 54px;
  padding: 36px 10px;

  background-color: $color-default-black;
  transform: rotate(-4.29deg);

  @include viewport--sm {
    min-height: 127px;
  }

  @media (max-width: 500px) {
    padding-top: 20px;
    padding-bottom: 10px;
    margin-bottom: 20px;
    width: 180px;
  }

  img {
    margin-left: -10px;
    margin-bottom: 10px;
    transform: rotate(4.29deg);
    width: 80%;

    @media (max-width: 1000px) {
      margin-bottom: 5px;
    }

    @media (max-width: 500px) {
      margin-bottom: 10px;
      height: 60%;
    }
  }

  h1,
  h2 {
    margin: 0;
    margin-left: -10px;
    padding: 0;
    font-weight: 700;
    font-size: 21px;
    line-height: 29px;
    letter-spacing: 0.03em;
    color: $color-default-white;

    @media (max-width: 1200px) {
      font-size: calc(1rem + 0.15vw);
    }

    @media (max-width: 1000px) {
      width: 100%;
      font-size: calc(1rem + 0.25vw);
      text-align: center;
    }

    @media (max-width: 800px) {
      font-size: calc(1rem + 0.15vw);
      line-height: 30px;
      text-align: center;
    }
  }
}

.text__title-benefit {
  h1,
  h2 {
    margin: 0;
    padding: 0;
    max-width: 524px;
    font-style: 700;
    font-size: 55px;
    line-height: 77px;

    @media (max-width: 1000px) {
      font-size: 41px;
      line-height: 65px;
    }

    @media (max-width: 570px) {
      font-size: 31px;
      line-height: 44px;
    }

    @media (max-width: 570px) {
      font-size: 28px;
      line-height: 32px;
    }
  }
}

.text__slogan {
  padding-left: 55px;

  @include viewport--sm {
    padding-left: 10px;
  }

  p {
    margin: 0;
    padding: 0;

    font-weight: 700;
    font-size: 24px;
    line-height: 34px;
    color: $color-default-black;

    @media (max-width: 900px) {
      font-size: 24px;
      line-height: 32px;
    }

    @media (max-width: 600px) {
      font-size: 16px;
    }
  }
}
