//- Кнопки
.benefit__list {
  display: flex;
  justify-content: space-between;
  flex-direction: row;
  flex-wrap: wrap;
  width: 100%;
  margin: 0;
  margin-top: 39px;
  margin-bottom: 22px;
  padding: 0;
  list-style: none;

  @include viewport--sm {
    flex-direction: column;
    flex-wrap: nowrap;
    align-content: center;
    margin-bottom: 25px;
  }

  .benefit__item {
    margin: 0;
    padding-right: 20px;

    @include viewport--sm {
      padding-right: 0;
    }

    @include viewport--sm {
      max-height: 58px;
      height: 100%;
    }

    &:last-child {
      padding-right: 0;
    }
  }

  input[type="radio"] {
    visibility: hidden;
    height: 0;
    width: 0;
  }

  label {
    display: flex;
    vertical-align: middle;
    align-items: center;
    justify-content: center;
    text-align: center;

    padding: 5px 29px 10px;
    max-width: 258px;
    min-height: 47px;

    font-size: 22px;
    line-height: 31px;
    text-transform: lowercase;
    color: $color-eclipse;
    border: 3px solid $color-inch-worm;
    border-radius: 67px;

    transition: color $default-transition-settings, background-color $default-transition-settings;
    user-select: none;
    cursor: pointer;

    @media (max-width: 1120px) {
      max-width: 200px;
      font-size: 18px;
    }

    @media (max-width: 975px) {
      max-width: 180px;
      font-size: 14px;
    }

    @media (max-width: 820px) {
      max-width: 150px;
      font-size: 14px;
    }

    @include viewport--sm {
      max-width: 100%;

      font-size: 22px;
      line-height: 24px;
    }

    @media (max-width: 400px) {
      font-size: 18px;
      line-height: 25px;
    }
  }

  input[type="radio"]:checked + label {
    background-color: $color-inch-worm;
    color: $color-default-black;
    padding: 5px 29px 10px;
    pointer-events: none;
  }

  input[type="radio"]:hover:not(:checked) + label {
    background-color: rgba($color-inch-worm, 0.3);
    border: 3px solid $color-inch-worm;
    border-radius: 67px;
    color: $color-eclipse;
  }
}
