.directions {
  margin-top: 48px;
  margin-bottom: 87px;

  @include viewport--sm {
    margin-top: 23px;
    margin-bottom: 60px;
  }
}

.directions h2 {
  display: block;
  max-width: 450px;
  margin: 0 auto;
  font-style: normal;
  font-weight: normal;
  font-size: 23px;
  line-height: 32px;
  text-align: center;
  color: $color-nero;

  @include viewport--sm {
    font-size: 16px;
    line-height: 22px;
  }
}

.directions__list {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  margin: 0;
  margin-top: 35px;
  padding: 0;
  list-style: none;
}

.directions__list-item {
  width: 48.826%;
  margin-bottom: 23px;
  padding: 21px 2.95% 34px 4.796%;
  border: 1.625px solid $color-default-black;
  box-sizing: border-box;
  border-radius: 7.3125px;

  @include viewport--sm {
    width: 100%;
  }
}

.directions__flex-container {
  display: flex;
  justify-content: space-between;
}

.directions__list-item h3 {
  margin-top: 12px;
  margin-bottom: 32px;
  font-family: $font-family--tele2-serif;
  font-style: normal;
  font-weight: bold;
  font-size: 36px;
  line-height: 51px;
  color: $color-default-black;

  @media (max-width: 1200px - 1) {
    font-size: 32px;
    line-height: 48px;
  }

  @include viewport--md {
    font-size: 27px;
    line-height: 43px;
  }
}

.directions__list-item p {
  margin: 0;
  font-family: $font-family--tele2-serif;
  font-style: normal;
  font-weight: normal;
  font-size: 20.3125px;
  line-height: 26px;
  letter-spacing: -0.5px;
  color: $color-silver;

  @include viewport--md {
    font-size: 18px;
    line-height: 26px;
  }
}

.directions__flex-container:last-of-type p {
  max-width: 65%;
  font-size: 18.6875px;
  line-height: 24px;
  color: $color-default-black;

  @include viewport--md {
    font-size: 16px;
    line-height: 20px;
  }
}

// доп.класс bonus по просьбе бэка
.directions__list-item span,
.bonus {
  margin-right: 15px;
  padding: 2px 7px 5px;
  font-family: $font-family--tele2-serif;
  font-style: normal;
  font-weight: bold;
  font-size: 15px;
  line-height: 21px;
  color: $color-nero;
  background: $color-dark-tangerine;
  border-radius: 4px;

  @include viewport--md {
    font-size: 13px;
    line-height: 18px;
  }

  @include viewport--sm {
    margin-right: 0;
  }
}

.directions__list-item span {
  margin-right: 0;
}

.directions__flex-container:last-of-type span {
  display: block;
  margin: 4px 15px 23px;
  padding: 0 7px 1px;
  font-size: 19px;
  line-height: 27px;
  color: $color-grey;
  background: transparent;
  text-align: center;
  border: 1px solid $color-very-light-grey;
  border-radius: 8px;

  @include viewport--md {
    margin-bottom: 14px;
    font-size: 16px;
    line-height: 23px;
  }
}

.directions__link {
  display: block;
  width: 77px;
  height: 77px;
  outline: none;

  @include viewport--md {
    width: 66px;
    height: 66px;
  }
}

.directions__link:hover svg,
.directions__link:focus svg {
  fill: $color-nero;
  transition: fill $default-transition-settings;
}

.directions__link:active svg {
  fill: rgba($color-nero, 0.7);
  transition: fill $default-transition-settings;
}

.directions__list-item svg {
  fill: $color-inch-worm;

  @include viewport--md {
    width: 66px;
    height: 66px;
  }
}
