@font-face {
  font-style: normal;
  font-weight: 700;
  font-family: "Tele2-DisplaySerif";

  src:
    url("../fonts/Tele2DisplaySerif-Bold.woff2") format("woff2"),
    url("../fonts/Tele2DisplaySerif-Bold.woff") format("woff"),
    url("../fonts/Tele2DisplaySerif-Bold.ttf") format("truetype");
}

@font-face {
  font-style: normal;
  font-weight: 400;
  font-family: "Tele2-DisplaySerif";

  src:
    url("../fonts/Tele2DisplaySerif-Regular.woff2") format("woff2"),
    url("../fonts/Tele2DisplaySerif-Regular.woff") format("woff"),
    url("../fonts/Tele2DisplaySerif-Regular.ttf") format("truetype");
}
