.modal-professional-picture,
.modal-bodily-picture,
.modal-emotionally-picture,
.modal-social-picture {
  position: absolute;
  bottom: 0;
  right: 0;
  visibility: hidden;
  opacity: 0;
  transform: translateX(150%);
  transition: transform $default-transition-modal,
    right $default-transition-modal,
    opacity $default-transition-modal,
    visibility $default-transition-modal;
}

.active-picture {
  position: absolute;
  bottom: 0;
  right: 13.2%;
  visibility: visible;
  opacity: 1;
  transform: translateX(0);
  transition: transform $default-transition-modal,
    right $default-transition-modal,
    opacity $default-transition-modal,
    visibility $default-transition-modal;

  @include viewport--lg {
    right: 10.5%;
  }

  @include viewport--xs {
    right: -3.5%;
  }
}

.modal-professional-list,
.modal-bodily-list,
.modal-emotionally-list,
.modal-social-list {
  display: none;
  opacity: 1;
  transform: translateX(0);
  transition: transform $default-transition-modal,
    opacity $default-transition-modal;
}

.active-list {
  display: inline-block;
  opacity: 1;
  transform: translateX(0);
  animation-name: transform-list;
  animation-duration: 0.8s;
  animation-fill-mode: forwards;
  transition: transform $default-transition-modal,
    opacity $default-transition-modal;
}

.text__title-benefit {
  position: relative;

  h1 {
    @include visually-hidden;
  }

  h2 {
    position: absolute;
    width: 38.473%;
    visibility: hidden;
    opacity: 0;
    transition: opacity $default-transition-settings,
      visibility $default-transition-settings;
  }

  .active-title {
    position: absolute;
    width: 38.473%;
    height: max-content;
    visibility: visible;
    opacity: 1;
    transition: opacity $default-transition-settings,
      visibility $default-transition-settings;
  }
}

@keyframes transform-list {
  0% {
    opacity: 0;
    transform: translateX(-150%);
  }

  100% {
    opacity: 1;
    transform: translateX(0);
  }
}
