.header {
  position: relative;
  display: flex;
  min-height: 675px;
  overflow: hidden;

  @media (max-width: 400px) {
    min-height: 580px;
  }

  @include viewport--xs {
    min-height: 538px;
  }

  &--benefit {
    min-height: 518px;

    @include viewport--sm {
      min-height: 540px;
    }

    @media (max-width: 650px) {
      min-height: 515px;
    }
  }

  .header__img-triangle {
    position: absolute;
    bottom: 0;
    left: 0;
    z-index: 2;

    content: "";
    width: 0;
    height: 0;
    border-style: solid;
    border-width: 0 100vw 354.5px 100vw;
    border-color: transparent transparent $color-inch-worm transparent;

    mix-blend-mode: multiply;
    overflow: hidden;

    // свойство только для IE, mix-blend-mode IE не поддерживает
    @media screen and (-ms-high-contrast: active), (-ms-high-contrast: none) {
      opacity: 0.6;
    }

    @include viewport--sm {
      border-width: 0 758px 269px 758px;
    }

    @media (max-width: 400px) {
      border-width: 0 400px 269px 400px;
    }

    &--benefit {
      border-width: 0 100vw 279px 100vw;

      @include viewport--sm {
        border-width: 0 758px 269px 758px;
      }

      @media (max-width: 400px) {
        border-width: 0 400px 269px 400px;
      }
    }
  }
}

.header__wrapper {
  position: relative;
  margin: 0 auto;
  padding-top: 65px;
  width: 1440px;

  @media (min-width: 1440px) {
    padding: 65px 0 0 0;
  }

  @include viewport--lg {
    padding-top: 15px;
    width: 100%;
  }

  .header__meta {
    position: absolute;
    top: 65px;
    left: 9.5%;
    display: flex;
    flex-direction: row;
    margin-bottom: 53px;
    padding-left: 71px;

    @include viewport--lg {
      top: 30px;
      padding-left: 0;
    }

    @media (max-width: 400px) {
      top: 20px;
      left: 4.5%;
    }

    p {
      position: relative;
      width: 92px;
      margin: 0;
      padding: 0;

      font-weight: 700;
      font-size: 16px;
      line-height: 18px;
      text-transform: uppercase;
      color: $color-default-black;

      @media (max-width: 400px) {
        width: 80px;
        font-size: 14px;
      }

      &::before {
        position: absolute;
        top: 2px;
        right: 0;
        content: "";
        width: 1.5px;
        height: 32px;
        background-color: $color-default-black;
      }
    }

    ul {
      list-style: none;
      margin: 0;
      padding: 0;
      padding-left: 15px;

      li {
        font-weight: 700;
        font-size: 12px;
        line-height: 12px;
        text-transform: uppercase;
        color: $color-default-black;

        @media (max-width: 400px) {
          font-size: 9px;
        }
      }
    }
  }

  .header__img {
    position: absolute;
    bottom: 0;
    right: -27px;

    object-fit: contain;

    width: 919px;
    height: 610px;

    @media (min-width: 1440px) {
      right: 108px;
    }

    @media (max-width: 768px) {
      width: 375px;
      height: 453px;
      right: 0;
    }

    @include viewport--xs {
      width: 300px;
      height: 362px;
      right: 0;
    }
  }

  .header__img-benefit {
    max-width: 503px;
    max-height: 496px;

    @media (max-width: 1100px) {
      right: 0;
      max-width: 400px;
      max-height: 400px;
    }

    @media (max-width: 850px) {
      max-width: 300px;
      max-height: 300px;
    }

    @media (max-width: 650px) {
      max-width: 312px;
      max-height: 321px;
    }
  }
}
