.benefit {
  margin-bottom: 60px;
}

.benefit > h2 {
  @include visually-hidden;
}

.benefit__list {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  margin: 0;
  padding: 0;
  list-style: none;
}

.benefit__list-item {
  width: 100%;
  min-height: 216px;
  margin-bottom: 23px;
  padding: 30px 3.719% 22px 3.774%;
  border: 1.625px solid $color-default-black;
  box-sizing: border-box;
  border-radius: 7.3125px;

  @include viewport--sm {
    min-height: 424px;
    position: relative;
    margin-bottom: 16px;
    padding: 8px 17px 16px 25px;

    .benefit__span-bonus {
      position: absolute;
      top: -322px;
      right: 0;
    }
  }
}

.benefit__flex-container-row {
  display: flex;
  flex-direction: row;

  @include viewport--sm {
    flex-direction: column;
  }
}

.benefit__flex-container-column {
  display: flex;
  flex-direction: column;
}

.benefit__text-container {
  width: 49.73%;

  @include viewport--sm {
    width: 100%;
    text-align: center;
  }
}

.benefit__icon {
  margin-top: -4px;
  width: 160px;
  height: 160px;
  margin-right: 5.2%;
  display: flex;
  align-items: center;
  justify-content: center;

  img {
    max-width: 160px;
    max-height: 160px;
    object-fit: none;
  }

  @include viewport--sm {
    width: 145px;
    height: 145px;

    img {
      max-width: 145px;
      max-height: 145px;
      object-fit: contain;
    }
  }
}

.benefit__list-item h3 {
  margin: 0;
  margin-bottom: 10px;
  padding: 0;

  font-weight: 700;
  font-size: 31px;
  line-height: 44px;
  color: $color-nero;

  @include viewport--sm {
    margin-bottom: 15px;
    font-size: 23px;
    line-height: 29px;
    text-align: left;
  }
}

.benefit__list-item p {
  max-width: 433px;
  margin: 0;
  font-size: 19px;
  line-height: 27px;
  color: $color-nero-2;
  text-align: left;

  @include viewport--md {
    font-size: 18px;
    line-height: 26px;
    max-width: 90%;
  }

  @include viewport--sm {
    font-size: 16px;
    line-height: 22.46px;
    max-width: 273px;
  }
}

.benefit__col-last {
  position: relative;
  padding: 10px 10px;
  width: 22.76%;
  min-height: 142px;
  margin-left: auto;

  @include viewport--sm {
    min-height: 70px;
    min-width: 140px;
    width: 100%;
    margin: 0;
    margin-top: auto;
    margin-left: auto;
    padding: 0;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: flex-end;
  }

  .benefit__flex-container-row {
    justify-content: space-between;
  }

  .benefit__button {
    position: absolute;
    bottom: 15px;
    right: 0;
    display: block;
    width: 66px;
    height: 66px;
    margin: 0;
    padding: 0;
    background-color: transparent;
    border: 0;
    border-radius: 50%;
    outline: none;
    cursor: pointer;

    @include viewport--sm {
      position: relative;
    }

    @include viewport--lg {
      bottom: 0;
    }

    & svg {
      width: 66px;
      height: 66px;
      fill: $color-inch-worm;
      transform: rotate(90deg);
      transition: transform $default-transition-settings,
        fill $default-transition-settings;
    }
  }
}

.benefit__button:hover svg,
.benefit__button:focus svg {
  fill: $color-nero;
}

.benefit__button:active svg {
  fill: rgba($color-nero, 0.7);
}

.benefit__button.benefit__button--arrow-rotate svg {
  fill: $color-nero;
  transform: rotate(270deg);
  transition: fill $default-transition-settings,
    transform $default-transition-settings,
    opacity $default-transition-settings;
}

.benefit__button.benefit__button--arrow-rotate:hover svg,
.benefit__button.benefit__button--arrow-rotate:focus svg {
  opacity: 0.7;
}

.benefit__button.benefit__button--arrow-rotate:active svg {
  fill: $color-nero;
  opacity: 1;
}

.benefit__span-bonus {
  margin-right: 15px;
  padding: 2px 7px 5px;

  font-weight: bold;
  font-size: 15px;
  line-height: 21px;
  color: $color-nero;

  background: $color-dark-tangerine;
  border-radius: 4px;

  @include viewport--md {
    font-size: 13px;
    line-height: 18px;
  }

  @include viewport--sm {
    min-width: 115px;
    margin-top: 15px;
    margin-right: 0;
    padding: 3px 7px 6px 7px;
    text-align: center;
  }

  @media (max-width: 330px) {
    font-size: 10px;
  }
}

.benefit__span-number {
  margin-left: auto;
  min-width: 48px;
  max-height: 30px;
  padding: 0 7px 1px;
  font-size: 19px;
  line-height: 27px;
  color: $color-grey;
  background: transparent;
  text-align: center;
  border: 1px solid $color-very-light-grey;
  border-radius: 8px;

  @include viewport--md {
    font-size: 16px;
    line-height: 23px;
  }

  @include viewport--sm {
    min-height: 22px;
    min-width: 35.2px;
    margin-left: 0;
    margin-right: 14px;
    padding: 2px 7px 2px;
    font-size: 14px;
    line-height: 20px;
  }
}
